.hero {
    background-image: url('https://www.pizzanapoletana.org/struttura/pagine_bicolor/mobile/disciplinare_avpn_2.jpg'); /* Background image */
    background-size: cover; /* Cover the entire section */
    background-position: center; /* Center the image */
    height: 400px; /* Adjust height as needed */
    display: flex; /* Center the content */
    align-items: center; /* Vertically center */
    justify-content: center; /* Horizontally center */
    color: white; /* Text color */
    text-align: center; /* Center align text */
    padding: 20px; /* Padding for text */
    position: relative; /* For positioning if needed */
    margin: 0;
}

/* Optional: Style for the heading */
.hero-content h2 {
    font-size: 1.82em; /* Adjust font size as needed */
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7); /* Shadow for better readability */
    margin: 0; /* Remove default margin */
}
