/* Home.css */

.content-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }
  
  /* You can add more specific styles for each component as needed.
     For example, to style the Hero component: */
  
  .hero {
    /* Your Hero component styles here */
    background-color: #f0f0f0;
    padding: 30px;
    text-align: center;
  }
  .call-button {
    display: inline-block; /* Size by content */
    margin-top: 15px;
    padding: 12px 20px; /* Increased padding for a bigger button */
    background-color: #28a745; /* Green color for call button */
    color: white;
    text-align: center;
    border-radius: 5px;
    text-decoration: none; /* Remove underline */
    cursor: pointer;
    font-weight: bold;
    font-size: 1.6em; /* Increased font size for better visibility */
    border: none;
}

.call-button:hover {
    background-color: #218838; /* Darker green on hover */
}


  /* And so on for the Menu, Description, and ContactForm components. */