/* Header container */
.header {
    display: flex;
    justify-content: space-between; /* Space between logo and nav */
    align-items: center;
    background-color: #f4f1eb; /* Soft red for pizza theme */
    padding: 15px 30px; /* Optional: adjust as needed */
    border-bottom: 2px solid #e0e0e0; /* Light border */
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1); /* Soft shadow */
    margin: 0; /* Ensure no space at the top */
    direction: rtl; /* Set the reading direction to right-to-left */
    position: fixed; /* Fix the header to the top */
    top: 0; /* Position it at the top */
    left: 0; /* Align it to the left */
    right: 0; /* Stretch it to the right */
    width: 100%; /* Ensure it covers full width */
    z-index: 1000; /* Keep it above other content */
}

/* Logo container */
.logo-text {
    text-align: right; /* Align logo text to the right */
    margin-right: 30px; /* Adjust margin to bring it away from the edge */
}

/* Main logo text */
.logo-main {
    font-size: 2em;
    font-weight: bold;
    color: #000;
    display: inline-block;
}

.logo-dot {
    color: #000;
    font-size: 1.5em;
    vertical-align: top;
    margin-right: 2px; /* Adjust for right-to-left */
}

/* Subtext styling */
.logo-subtext {
    font-size: 0.8em;
    color: #333;
    margin: 0;
    line-height: 1.2em;
    font-weight: lighter;
}

/* Navigation links */
.nav {
    display: flex;
    gap: 20px;
}

/* Update nav-link margin for RTL */
.nav-link {
    color: #000;
    text-decoration: none;
    font-weight: bold;
    padding: 5px 10px;
    border-radius: 5px;
    transition: background-color 0.3s, color 0.3s;
}

/* Hover effect for links */
.nav-link:hover {
    background-color: #ffffff;
    color: #000000;
    box-shadow: 0px 2px 8px rgba(255, 255, 255, 0.3);
}

/* Active link effect */
.nav-link:active {
    transform: scale(0.95); /* Click effect */
}
.menu-link {
    color: #000; /* Same color as other links */
    text-decoration: none; /* No underline */
    padding: 5px 10px; /* Optional padding */
    border-radius: 5px; /* Optional rounded corners */
    transition: background-color 0.3s, color 0.3s; /* Smooth transition */
}

.menu-link:hover {
    background-color: #f0f0f0; /* Change background on hover */
    color: #007bff; /* Change color on hover */
}
