.description-text {
  background-color: rgba(255, 255, 255, 0.8);
  color: #000;
  padding: 20px;
  text-align: right;
  margin: 20px;
  border-radius: 8px;
  max-width: 800px;
  margin-left: 500px;
  margin-right: 0px; /* Adjust right margin to control distance from right edge */
}

.description-text h3 {
  font-size: 2.5em; /* Increased font size */
  margin: 0 0 20px 0;
  font-family: 'Rubik Bubbles', cursive;
}

.description-text p {
  font-family: 'Rubik Moonrocks', sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 1.5em; /* Increased font size */
  line-height: 1.5;
  margin: 0;
}

.link-container {
    display: flex;
    justify-content: space-between;
    width: 80%; /* Adjust the width as needed */
}

.link-container a {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px; /* Add margin between links */
}

.link-container img {
  border-radius: 50%;
  width: 150px;
  height: 150px;
  object-fit: cover;
  border: 4px solid black;
}

.link-container figure {
  text-align: center;
}

.link-container figcaption {
  font-size: 18px;
  margin-top: 5px;
}